import {VERTICAL} from './constants.js';
import '@servicenow/now-button';
import '@servicenow/now-heading';
import isEmpty from 'lodash/isEmpty';
import '@devsnc/sn-declarative-actions';
import {t} from 'sn-translate';
import {getAdjustedCardWidth} from './utils';


const ViewAll = ({label}) => (
	<now-button-bare
		label={t(label)}
		iconEnd='arrow-end-fill'
		variant='primary'
		size='sm'>
	</now-button-bare>
);

const EmptyDataRow = ({noDataMessage}) => <div className='now-data-row-empty-placeholder'>{noDataMessage}</div>;

const ContentHtml = ({htmlFragments, noDataMessage, isLayoutVertical, cardHeightHorizontal}) => {
	if (isEmpty(htmlFragments))
		return <EmptyDataRow noDataMessage={noDataMessage} />;

	const className = !isLayoutVertical ? `now-content-card-size-${cardHeightHorizontal}` : '';
	return htmlFragments.map(card => <div className={className}>{card}</div>);
};

const mediatorWrapper = view => <sn-declarative-mediator should-wrap-action={true}>{view}</sn-declarative-mediator>;

const DataRowHeader = ({title, displayViewAll, viewAllLinkLabel}) => {
	const viewAll = displayViewAll ? <ViewAll label={viewAllLinkLabel} /> : '';
	if (title || displayViewAll) {
		return (
			<div className='now-data-row-header'>
				<now-heading label={title} level="1" variant="header-secondary" hasNoMargin="true"></now-heading>
				{viewAll}
			</div>
		);
	}
};

const DataRowFooter = ({displayViewAll, viewAllLinkLabel}) => {
	if (displayViewAll) {
		return (
			<div className='now-data-row-footer'>
				<ViewAll label={viewAllLinkLabel} />
			</div>
		);
	}
};

export default state => {
	const {title, layout, noDataMessage, htmlFragments, maxVisibleCardsHorizontal, minCardPixelWidth,
		cardHeightHorizontal, viewAllLinkLabel, noBox} = state.properties;
	const isLayoutVertical = layout === VERTICAL;
	const box = noBox ? '' : '-box';
	const hasViewAll = viewAllLinkLabel && viewAllLinkLabel !== '';

	const cssVars = {
		'--now-data-row-max-visible-cards': maxVisibleCardsHorizontal,
		'--now-data-row-min-card-width': getAdjustedCardWidth(state.parentWidth, minCardPixelWidth, maxVisibleCardsHorizontal, noBox)
	};
	const isEmpty = htmlFragments && htmlFragments.length > 0 ? '' : '-is-empty';

	return mediatorWrapper(
		<div className={`now-data-row-root-element -${layout} ${box}`}>
			<DataRowHeader title={title} displayViewAll={hasViewAll && !isLayoutVertical}
				viewAllLinkLabel={viewAllLinkLabel}></DataRowHeader>
			<div className={`now-data-row-container ${isEmpty}`} style={cssVars}>
				<ContentHtml
					htmlFragments={htmlFragments}
					noDataMessage={noDataMessage}
					isLayoutVertical={isLayoutVertical}
					cardHeightHorizontal={cardHeightHorizontal} />
			</div>
			<DataRowFooter displayViewAll={hasViewAll && isLayoutVertical}
				viewAllLinkLabel={viewAllLinkLabel}></DataRowFooter>
		</div>
	);
};
