import {t} from 'sn-translate';
import {resolve} from '@devsnc/sn-evam-resolver';
import truncate from 'lodash/truncate';
import isEmpty from 'lodash/isEmpty';
import lowerCase from 'lodash/lowerCase';
import {DEF_MIN_CARD_WIDTH, CARD_GAP} from './constants';
import {contractGenerator, transformer, EVAM} from '@devsnc/sn-declarative-actions';

export const transformState = state => {
	const {layout, title, noDataMessage, data, minCardPixelWidth} = state.properties;
	const items = !isEmpty(data) && Array.isArray(data.items) ? data.items : [];
	const properties = {
		...state.properties,
		layout: lowerCase(layout),
		title: t(truncate(title, {length: 63})),
		noDataMessage: t(truncate(noDataMessage, {length: 203})),
		htmlFragments: !isEmpty(items) ? resolve(items) : [],
		minCardPixelWidth: Math.max(minCardPixelWidth, DEF_MIN_CARD_WIDTH)
	};
	return {...state, properties};
};

export const transformDAClientActionToUXEvents = declarativeActions => {
	if (isEmpty(declarativeActions)) 
		return {};

	const daAction = transformer(declarativeActions);
	return contractGenerator(daAction, {modelType: EVAM}) || {};
};

export const getActionPayload = (state, updateState, payload, meta) => {
	try {
		const {actionsInProcess} = state;
		const assignmentId = meta.request.updatedUrl.split('declarative_action')[1].slice(1);
		const systable = meta.request.params.sysparm_table;
		const {sysId} = meta.request.data;
		const key = `${assignmentId}_${systable}_${sysId}`;
		const emitPayload = actionsInProcess.get(key);
		actionsInProcess.delete(key);
		emitPayload.declarativeAction_meta = meta;
		emitPayload.declarativeAction_payload = payload;
		updateState({actionsInProcess});
		return emitPayload;
	} catch (e) {
		return;
	}
};

export const getAdjustedCardWidth = (parentWidth, minCardPixelWidth, maxVisibleCardsHorizontal, noBox) => {
	if (!parentWidth) 
		return `${minCardPixelWidth}px`;

	const boxMargin = noBox ? 0 : CARD_GAP;
	const availableWidth = parentWidth - (boxMargin + (maxVisibleCardsHorizontal * CARD_GAP));
	const cardWidthAdjustedToScreen = Math.floor(availableWidth / maxVisibleCardsHorizontal);
	
	if (cardWidthAdjustedToScreen > minCardPixelWidth) 
		return `${cardWidthAdjustedToScreen}px`;
	maxVisibleCardsHorizontal--;

	return getAdjustedCardWidth(parentWidth, minCardPixelWidth, maxVisibleCardsHorizontal, noBox);

};

