import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import isEmpty from 'lodash/isEmpty';
import styles from './styles.scss';
import {
	HORIZONTAL, NOW_BUTTON_BARE_CLICKED, NOW_DATA_ROW_VIEW_ALL_CLICKED, DEF_MAX_VISIBLE_CARDS,
	DEF_MIN_CARD_WIDTH, DEF_CARD_HEIGHT, DEF_EMPTY_MSG, DA_WRAPPED_CLICKED, VIEW_ALL,
	DECLARATIVE_ACTION_SCRIPT_SUCCESS, DECLARATIVE_ACTION_SCRIPT_FAILED, NOW_DATA_ROW_ACTION_SUCCEEDED,
	NOW_DATA_ROW_ACTION_FAILED, DECLARATIVE_ACTION, ACTION_TYPE_SERVER_SCRIPT
} from './constants';
import view from './view';
import {transformState, transformDAClientActionToUXEvents, getActionPayload} from './utils';
import rtlBehavior from '@servicenow/behavior-rtl';

/**
 * A data set displayed as a horizontal or vertical row of cards, 
 * with options to provide access to the larger data set, or to individual records.
 *
 * ## now-data-row
 *
 * @summary A single, scrollable row of cards containing actions that provide access to
 * either a larger data set or to individual records. 
 * @seismicElement now-data-row
 */
createCustomElement('now-data-row', {
	renderer: {type: snabbdom},
	view,
	properties: {
		/**
		 * Header message to display.
		 * @type {string}
		 */
		title: {
			default: '',
			schema: {
				type: 'string'
			}
		},
		/**
		 * Display view of the data row.
		 * Default: horizontal.
		 * @type {string}
		 */
		layout: {
			default: HORIZONTAL,
			schema: {
				type: 'string'
			}
		},
		/**
		 * Message to display when no content is present.
		 * @type {string}
		 */
		noDataMessage: {
			default: DEF_EMPTY_MSG,
			schema: {
				type: 'string'
			}
		},
		/**
		 * Text for the action to view all records.
		 * Default: "View All".
		 * @type {string}
		 */
		viewAllLinkLabel: {
			default: VIEW_ALL,
			schema: {
				type: 'string'
			}
		},
		/**
		 * The data input from the data resource.
		 * @type {object}
		 */
		data: {
			default: {},
			schema: {
				type: 'object'
			}
		},
		/**
		 * The maximum number of cards visible per row in the horizontal view.
		 * Must be between one and six cards.
		 * @type {enum}
		 */
		maxVisibleCardsHorizontal: {
			default: DEF_MAX_VISIBLE_CARDS,
			schema: {
				type: 'number',
				enum: ['1', '2', '3', DEF_MAX_VISIBLE_CARDS, '5', '6']
			}
		},
		/**
		 * While in grid view, the minimum width of each card in pixels.
		 * @type {number}
		 */
		minCardPixelWidth: {
			default: DEF_MIN_CARD_WIDTH,
			schema: {
				type: 'number'
			}
		},
		/**
		 * Sets card height in the horizontal view.
		 * ('sm'|'md'|'lg') default is 'sm'
		 * @type {('sm'|'md'|'lg')}
		 */
		cardHeightHorizontal: {
			default: DEF_CARD_HEIGHT,
			schema: {
				type: 'string',
				enum: ['sm', 'md', 'lg']
			}
		},
		/**
		 * When true, displays the data row without a box.
		 * @type {boolean}
		 * @uib.label No-Box state
		 * @uib.description Select to remove the box from the data row.
		 */
		noBox: {
			default: false,
			schema: {
				type: 'boolean'
			}
		},
		/**
		 * Object containing the data needed for dynamic declarative action event mapping in UI Builder.
		 * @type {DaClientActionContract}
		 */
		daClientActionContract: {
			default: {},
			computed({properties: {data}}) {
				const declarativeActions = !isEmpty(data) && Array.isArray(data.all_actions) ? data.all_actions : [];
				return transformDAClientActionToUXEvents(declarativeActions);
			},
			schema: {
				type: 'object'
			},
			selectable: true
		}
	},
	styles,
	transformState,
	initialState: {
		parentWidth: null,
		actionsInProcess: new Map()
	},
	actionHandlers: {
		[NOW_BUTTON_BARE_CLICKED]: ({dispatch}) => {
			dispatch(NOW_DATA_ROW_VIEW_ALL_CLICKED);
		},
		[actionTypes.COMPONENT_RENDERED]: ({action: {payload: {host}}, updateState}) => {
			/**
			 * DEF0126555: UXF sn-layout use slot for grid layouts, component that puts into slot will have 
			 * parentElement as sn-layout, not each grid item. host.getBoundingClientRect().width will represent 
			 * component grid container width(in decimals) as it gets put into the slot.
			 */
			const parentWidth = host.getBoundingClientRect().width; // need exact width in decimals
			updateState({parentWidth});
		},
		[DA_WRAPPED_CLICKED]: ({dispatch, action: {payload}}) => {
			dispatch(payload.wrapped_action_name, payload.wrapped_payload);
		},
		[DECLARATIVE_ACTION_SCRIPT_SUCCESS]: ({state, dispatch, updateState, action: {payload, meta}}) => {
			const emitPayload = getActionPayload(state, updateState, payload, meta);
			if (!emitPayload)
				return;
			dispatch(NOW_DATA_ROW_ACTION_SUCCEEDED, emitPayload);
		},
		[DECLARATIVE_ACTION_SCRIPT_FAILED]: ({state, dispatch, updateState, action: {payload, meta}}) => {
			const emitPayload = getActionPayload(state, updateState, payload, meta);
			if (!emitPayload)
				return;
			dispatch(NOW_DATA_ROW_ACTION_FAILED, emitPayload);
		},
		[DECLARATIVE_ACTION]: ({state, updateState, action: {payload: {action, model}}}) => {
			try {
				if (action.actionType !== ACTION_TYPE_SERVER_SCRIPT)
					return;
				const {actionsInProcess} = state;
				const {assignmentId} = action;
				const systable = model.table;
				const {sysId} = model;
				const key = `${assignmentId}_${systable}_${sysId}`;
				actionsInProcess.set(key, {action, model});
				updateState({actionsInProcess});
			} catch (e) {
				return;
			}
		}
	},
	dispatches: {
		/**
		 * Dispatched when the View All link is clicked.
		 *
		 * @type {{}}
		 */
		NOW_DATA_ROW_VIEW_ALL_CLICKED: {},
		/**
		 * Dispatched with information of the component level action when a server-side action is successful.
		 *
		 * @type {{}}
		 */
		NOW_DATA_ROW_ACTION_SUCCEEDED: {},
		/**
		 * Dispatched with information of the component level action when a server-side action fails.
		 *
		 * @type {{}}
		 */
		NOW_DATA_ROW_ACTION_FAILED: {}
	},
	behaviors: [
		{
			behavior: rtlBehavior
		}
	]
});
